const thisYear = new Date();
const Footer = () => (
    <footer className="footer">
        <div className="footerContent">
            &#x00A9; Daniel McGhee {thisYear.getFullYear()}
        </div>
        <style jsx>
            {`

                .footerContent {
                    bottom: 0;
                    position: fixed;
                    height: 30px;
                    width: 100%;
                    padding: 100px 0;
                    font-size: 20px;
                    font-family: comic-sans;
                    text-align: center;
                    background-image: url(https://i.annihil.us/u/prod/marvel/i/mg/9/20/5102c774ebae7.jpg);
                    background-position: center;
                    background-size: cover;
                    opacity: .4;
                }
            `}
        </style>

    </footer>
);

export default Footer;